.topic {
	padding: 107px 0 0;
	background: #f9fbff;
	@include sm {
		padding-top: 80px; }
	&__main {
		padding-bottom: 15px;
		@include clr; }
	&__heading {
		position: relative;
		float: left;
		margin-bottom: -2px;
		width: calc(100% - 200px);
		font-size: 0;
		@include sm {
			float: none;
			margin: 0;
			width: 100%; } }
	&__id {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 100%;
		padding-bottom: 5px;
		color: $cool-gray;
		@include f(18px, 500, 21px);
		@include text-overflow;
		@include sm {
			position: static; } }
	&__title,
	&__meta {
		display: inline-block;
		vertical-align: bottom; }
	&__title {
		margin-right: 10px;
		@include sm {
			padding-bottom: 2px; } }
	&__meta {
		padding-bottom: 5px;
		color: $cool-gray;
		@include f(12px, null, 14px); }
	&__btn {
		float: right;
		@include sm {
			margin: 20px 0 0;
			float: none; } }
	&__menu {
		padding-top: 20px;
		@include clr;
		@include sm {
			padding-top: 0;
			margin-top: -40px; } }
	&__list {
		float: left;
		margin: 0 -15px;
		@include sm {
			float: none;
			clear: both;
			padding-top: 25px; }
		@include xs {
			padding-top: 0;
			margin: 0; } }
	&__item,
	&__control {
		display: inline-block;
		vertical-align: bottom;
		@include xs {
			display: block; } }
	&__item {
		padding: 0 15px;
		@include xs {
			padding: 0; }
		&:last-child {
			.topic__control:after {
				display: none; } } }
	&__control {
		position: relative;
		padding-bottom: 10px;
		color: rgba($dark-gray, 0.5);
		@include f(16px, null, 1.1875);
		cursor: pointer;
		@include nosel;
		transition: color 0.3s;
		@include xs {
			padding: 15px 0; }
		@include hover {
			color: $blue; }
		&.is-active {
			color: $dark-gray;
			// font-weight: 600
			&:before {
				opacity: 1;
				@include gpu; } }
		&:before,
		&:after {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0; }
		&:before {
			border-bottom: 5px solid $blue;
			opacity: 0;
			transform: translate3d(0,10px,0);
			transition: opacity 0.3s, transform 0.3s;
			@include xs {
				height: 3px; } }
		&:after {
			display: none;
			border-bottom: 1px solid $dark-gray;
			opacity: 0.1;
			@include xs {
				display: block; } } }
	&__history {
		position: relative;
		float: right;
		padding-left: 22px;
		color: $cool-gray;
		@include f(16px, null, 1.1875);
		cursor: pointer;
		@include nosel;
		transition: color 0.3s;
		@include sm {
			margin-bottom: 20px; }
		@include hover {
			color: $blue; }
		&.is-active {
			color: $blue; }
		&.is-disabled {
			color: #D8DCE1 !important;
			cursor: not-allowed; }
		&-icon {
			position: absolute;
			top: 50%;
			left: 0;
			margin-top: -0.55em;
			fill: currentColor;
			font-size: 17px; }
		&-text {
			display: block; } } }
