.section {
	margin-bottom: 40px;
	@include sm {
		margin-bottom: 20px; }
	&.is-editing {
		.section {
			&__editing {
				display: inline-block; }
			&__selection {
				display: none; } }
		.static-table {
			&__tr {
				padding-left: 35px;
				@include sm {
					padding-left: 0;
					padding-right: 30px; } }
			&__checkbox {
				display: block; } } }
	&.is-selected {
		.section {
			&__title {
				.title-name {
					display: none; } }
			&__selected {
				display: inline-block; } } }
	&__title {
		position: relative;
		margin-bottom: 10px;
		min-height: 35px;
		@include clr;
		.title-name {
			display: inline-block;
			margin-top: 7px;
			@include xs {
				margin: 0 0 10px; } } }
	&__selected {
		display: none;
		margin-top: 7px;
		@include f(15px, null, 20px);
		color: $cool-gray;
		@include xs {
			margin: 0 0 10px; } }
	&__btns {
		float: right;
		margin: -5px;
		font-size: 0;
		@include xs {
			float: none; } }
	&__editing {
		display: none; }
	&__btn {
		// &:not(:last-child)
		// 	margin-right: 10px
		margin: 5px; } }
